body{
    margin-top: 7px;
    background-color: #3b3b3b;
}
  .row {
    display: flex;
    justify-content: center;

  }
  h4{
    color: lightblue;
  }
  .square {
    width: 100px;
    height: 100px;
    font-size:70px;
    font-weight: bolder;
    background-color: #e0e0e0;
    border: 1px solid black;
    cursor: pointer;
  }
  .historyList{
    display: grid;
    justify-content: center;
  }
  .historyButtons{
    background-color: #2d2d2f;
    padding: 8px 39px;
    border-radius: 10px;
    color: #898484;
    border: .8px solid #66635e;
    margin: 5px;
    transition: all ease-in-out .3s;
    cursor: pointer;
  }
  .historyButtons:hover{
    background-color: #dad3d3;
    padding: 8px 39px;
    border-radius: 10px;
    color: #2a2929;
    transform: scaleX(1.1);
  }
  .historyButtons:active{
    background-color: #fbfbfb;
    padding: 8px 39px;
    border-radius: 10px;
    color: #000000;
    transform: scaleX(1.2);
  }
  @media (min-width: 1001px) {
    .game{
        display: flex;
        justify-content: center;
    }
   
  }